import React from 'react';
import { Link } from 'react-router-dom';

const SinglePost = ({ teamlist }) => {
    return (
        <div>
            <div className='slider-card' data-aos="zoom-in-up" data-aos-duration="1500">
                <div className='img-wrapper'>
                    <img src={teamlist.Image} className='img-fluid w-100'></img>
                </div>
                <div className='text-slider'>
                <h5>{teamlist.Name}</h5>
                <p className='paragarph'>{teamlist.designation}</p>
                </div>
            </div>
        </div>
    );
}

export default SinglePost;
