import React, { useState, useCallback, useRef, useEffect } from 'react'

const About = () => {
  useEffect(() => {
    window.scrollTo(0, 0)
  }, [])
  return (
    <div className='page-wrapper'>
      <section className='stay-updated-wrap'>
        <div className='container'>
          <div className='row align-items-center'>
            <div className='col-md-6'>
              <img src='./image/stay-updated.png' className='img-fluid'></img>
            </div>
            <div className='col-md-6'>
              <h2 className='heading-contact' data-aos="zoom-in-up" data-aos-duration="1000">Stay Updated </h2>
              <p className='paragarph-18'>Be the first to receive the latest Hone news, events and product updates. </p>
              <form data-aos="zoom-in-up" data-aos-duration="1000">
                <div class="form-group">
                  <label for="name">Full name</label>
                  <input type="text" class="form-control" id="exampleInputEmail1" aria-describedby="textHelp" placeholder="Enter your name" />
                  <small id="textHelp" class="form-text text-muted">We'll never share your name with anyone else.</small>
                </div>
                <div class="form-group">
                  <label for="exampleInputEmail1">Email</label>
                  <input type="email" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter your email e.g info@honeag.com" />
                  <small id="emailHelp" class="form-text text-muted">We'll never share your email with anyone else.</small>
                </div>
                <div class="form-group">
                  <label for="exampleInputEmail1">Phone</label>
                  <input type="email" class="form-control" id="exampleInputEmail1" aria-describedby="contactHelp" placeholder="+61 " />
                  <small id="contactHelp" class="form-text text-muted">We'll never share your phone with anyone else.</small>
                </div>
                <div class="form-group">
                  <label for="exampleInputEmail1">What are you interested in testing?</label>
                </div>
                <div class="form-check form-check-inline">
                  <input class="form-check-input" type="checkbox" id="inlineCheckbox1" value="option1"/>
                    <label class="form-check-label" for="inlineCheckbox1">Soil</label>
                </div>
                <div class="form-check form-check-inline">
                  <input class="form-check-input" type="checkbox" id="inlineCheckbox2" value="option2"/>
                    <label class="form-check-label" for="inlineCheckbox2">Leaf</label>
                </div>
                <div class="form-check form-check-inline">
                  <input class="form-check-input" type="checkbox" id="inlineCheckbox3" value="option3"/>
                    <label class="form-check-label" for="inlineCheckbox3">Wine</label>
                </div>
                <div className='d-block mt-md-5 mt-3'>
                  <button type="submit" href="#" target="_blank" class="btn btn-primary">Submit</button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
}

export default About;
