import React, {useEffect} from "react";
import '../App';
import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";
import { team } from '../data/data';
import Team from './team';
import { Link } from 'react-router-dom';

const responsive = {
  superLargeDesktop: {
    // the naming can be any, depends on you.
    breakpoint: { max: 4000, min: 3000 },
    items: 5
  },
  desktop: {
    breakpoint: { max: 3000, min: 1024 },
    items: 3
  },
  tablet: {
    breakpoint: { max: 1024, min: 575 },
    items: 2
  },
  mobile: {
    breakpoint: { max: 575, min: 0 },
    items: 1
  }
};
const Home = () => {  
  useEffect(() => {
    window.scrollTo(0, 0)
  }, [])
  return (
    <div className="page-wrapper">
      <section className='hone-lab-banner hone-lab-carbon-banner' style={{backgroundImage:"url(./image/hone-carbon-banner.png)"}}>
        <div className='slider-item'>
          <div className='items-slider-wrap container'>
            <div className='row align-items-center'>
              <div className='col-md-7'>
                <div className='text-slider carbon-hone'>
                  <h1>Soil carbon testing at scale</h1>
                  <p className="">Accurate, compliant, and rapid measurement for soil carbon and other indicators of soil health. </p>
                  <Link className="btn btn-primary mt-4" to="/contact-us">Enquire now</Link>
                </div>
              </div>
              <div className='col-md-5 img-slider'>
                {/* <img src='./image/honecarbon-banner.png' alt='' className='img-fluid w-100' /> */}
              </div>
            </div>
          </div>
        </div>
      </section>
      <section className='our-build-for pt-3 pt-md-5 pb-0'>
        <div className='container'>
          <div className='row'>
            <div className='col-md-12'>
              <h2 className='section-heading'>Built for</h2>
            </div>
            <div className='col-md-12'>
              <div className='tabs'>
                <ul className="nav nav-tabs border-0" id="myTab" role="tablist">
                  <li className="nav-item">
                    <a className="nav-link active" id="home-tab" data-toggle="tab" href="#home" role="tab" aria-controls="home" aria-selected="true">Carbon Project Managers</a>
                  </li>
                  <li className="nav-item">
                    <a className="nav-link" id="profile-tab" data-toggle="tab" href="#profile" role="tab" aria-controls="profile" aria-selected="false">Agribusiness</a>
                  </li>
                  <li className="nav-item">
                    <a className="nav-link" id="contact-tab" data-toggle="tab" href="#contact" role="tab" aria-controls="contact" aria-selected="false">Researchers</a>
                  </li>
                </ul>
                <div className="tab-content" id="myTabContent">
                  <div className="tab-pane fade show active" id="home" role="tabpanel" aria-labelledby="home-tab">
                    <div className='row align-items-center'>
                      <div className='col-md-6'>
                        <div className='product-item-wrap'>
                          <div className='text-card p-0'>
                            <h4 className='tabs-heading desktop-view'>Carbon Project Managers</h4>
                            <p className='paragarph'>Hone fulfills the measurement piece of a project for the Carbon Farming Initiative, delivering efficient, compliant, and reliable results. Every project is different, so Hone customises its offering for individual requirements.</p>
                            <Link to='/contact-us' className='btn btn-primary'>Enquire now</Link>
                          </div>
                        </div>
                      </div>
                      <div className='col-md-6'>
                        <h4 className='tabs-heading mobile-view'>Carbon Project Managers</h4>
                        <div className='image-section light-bg'>                          
                          <img src='image/hone-tab-carbon-1.png' className='img-fluid w-100'></img>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="tab-pane fade" id="profile" role="tabpanel" aria-labelledby="profile-tab">
                    <div className='row align-items-center'>
                      <div className='col-md-6'>
                        <div className='product-item-wrap'>
                          <div className='text-card p-0'>
                            <h4 className='tabs-heading desktop-view'>Agribusiness</h4>
                            <p className='paragarph'>Working with a range of agricultural businesses, Hone provides soil measurement capabilities for multiple uses, from fertiliser suppliers to precision agronomy. Contact Hone to tell us about your needs for soil testing.</p>
                            <Link to='/contact-us' className='btn btn-primary'>Enquire now</Link>
                          </div>
                        </div>
                      </div>
                      <div className='col-md-6'>
                        <h4 className='tabs-heading mobile-view'>Agribusiness</h4>
                        <div className='image-section light-bg'>                          
                          <img src='image/hone-tab-carbon-2.png' className='img-fluid w-100'></img>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="tab-pane fade" id="contact" role="tabpanel" aria-labelledby="contact-tab">
                    <div className='row align-items-center'>
                      <div className='col-md-6'>
                        <div className='product-item-wrap'>
                          <div className='text-card p-0'>
                            <h4 className='tabs-heading desktop-view'>Researchers</h4>
                            <p className='paragarph'>Hone works with a range of research institutions for rapid analysis of soil. Apart from nationally-accredited soil laboratories, Hone also provides a rapid measurement solution custom made for research needs.</p>
                            <Link to='/contact-us' className='btn btn-primary'>Enquire now</Link>
                          </div>
                        </div>
                      </div>
                      <div className='col-md-6'>
                      <h4 className='tabs-heading mobile-view'>Researchers</h4>
                        <div className='image-section light-bg'>                          
                          <img src='image/hone-tab-carbon-3.png' className='img-fluid w-100 '></img>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      <section className='py-3 py-md-5 video-main-section'>
        <div className='container'>
          <div className='row'>
            <div className='col-md-12 mb-5'>
              <h2 className='section-heading '>How it works</h2>
              <p className="paragarph-heading">Hone Carbon works collaboratively with project and asset managers to make quantification of carbon stocks simple.</p>
            </div>
            <div className='col-md-12 video-links'>
              <div className='how-it-work-items'>
                <div className='how-it-work-item'>
                  <div className='number-how-it-work'>
                    <img src='image/get-in-touch.svg' className='number-icon'></img>
                    <span>01</span>
                  </div>
                  <div className='content-how-it-work'>
                    <p className="paragarph-heading">Get in contact with Hone Carbon to discuss project scope, size and timeline.</p>
                  </div>                    
                </div>
                <div className='how-it-work-item'>
                  <div className='number-how-it-work'>
                  <img src='image/sendsamples.svg' className='number-icon'></img>
                    <span>02</span>
                  </div>
                  <div className='content-how-it-work'>
                  <p className="paragarph-heading">Route your samples to Hone Carbons soil processing facility for baseline measurement and spectral model development.</p>
                  </div>                    
                </div>
                <div className='how-it-work-item'>
                  <div className='number-how-it-work'>
                  <img src='image/leveragebenefits.svg' className='number-icon'></img>
                    <span>03</span>
                  </div>
                  <div className='content-how-it-work'>
                  <p className="paragarph-heading">Leverage the benefits of spectroscopy for simpler measurement with increased resolution for subsequent crediting rounds.</p>
                    {/* <p className='paragarph-heading'>Hones offering can be expanded to analyse beyond the bare minimum required for a CFI project. </p> */}
                  </div>                    
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      <section className='our-technologes hone-carbon-technologes'>
        <div className='container'>
          <div className='row'>
            <div className='col-md-12'>
              <h2 className='section-heading'>Our Technology </h2>
              <p className='paragarph-heading'>Hone Carbon is the culmination of many years of research and development.</p>
            </div>
          </div>
          <div className='row align-items-center our-technologes-items hone-carbon-items' data-aos="zoom-in-up" data-aos-duration="1000" style={{'backgroundImage': 'url(image/complete-img.png'}}>
            <div className='col-md-6'>
              <div className='product-item-wrap'>
                <div className='text-card'>
                  <h3 className='technologes-heading'>Complete Measurement Solution</h3>
                  <p className='paragarph'>Hone chain of custody measurement services provides asset managers with actionable results.</p>
                  <Link to='/stay-uplated' className='btn btn-primary mt-3 mt-md-4'>Stay Updated</Link>
                </div>
              </div>
            </div>
            <div className='col-md-6'>
            <div className='image-section mobile-view'>
                <img src='image/complete-img.png' className='img-fluid w-100 '></img>
              </div>
            </div>
          </div>
          <div className='row align-items-center our-technologes-items hone-carbon-items' data-aos="zoom-in-up" data-aos-duration="1000" style={{'backgroundImage': 'url(image/compliant-img.png'}}>
            <div className='col-md-6'>
              <div className='product-item-wrap'>
                <div className='text-card'>
                  <h3 className='technologes-heading'>Compliant with Government Requirements</h3>
                  <p className='paragarph'>The use of Hones technology is compliant with the Australian Clean Energy Regulators Carbon Farming Initiative.</p>
                </div>
              </div>
            </div>
            <div className='col-md-6'>
              <div className='image-section mobile-view'>
                <img src='image/compliant-img.png' className='img-fluid w-100 '></img>
              </div>
            </div>
          </div>
          <div className='row align-items-end our-technologes-items hone-carbon-items' data-aos="zoom-in-up" data-aos-duration="1000">
            <div className='col-md-6 pr-md-0 align-self-center'>
              <div className='product-item-wrap'>
                <div className='text-card'>
                  <h3 className='technologes-heading'>Test what you want</h3>
                  <p className='paragarph'>The Hone Lab Red can be leveraged to analyse a wide range of soil properties, not limited to carbon.</p>
                  <Link to='/stay-uplated' className='btn btn-primary mt-3 mt-md-4'>Stay Updated</Link>
                </div>
              </div>
            </div>
            <div className='col-md-6'>
              <div className='image-section what-you-want-img'>
               <img src='image/what-you-want-img.png' className='img-fluid w-100 shadow-md'></img>
              </div>
            </div>
          </div>
        </div>
      </section>
      <section className='section-Features future-section py-0'>
        <div className='container-fluid'>
          <div className='row align-items-center'>
            <div className='col-md-7'>
              <div className='product-item-wrap'>
                <div className='text-card'>
                  <h2 className='section-heading desktop-view text-white'>The Future Of In-Field Soil Analysis</h2>
                  <div className='d-flex align-items-start features-items'>
                    <img src='image/Spectroscopy.svg' alt='' className='img-fluid' />
                    <p className='paragarph'>Hone Carbon works with CFI Project Managers to fulfill measurement requirements, within regulations.</p>
                  </div>
                  <div className='d-flex align-items-start features-items'>
                    <img src='image/Reporting.svg' alt='' className='img-fluid' />
                    <p className='paragarph'>The team at Hone maintains familiarity with the complex regulatory measurement framework so you dont need to.</p>
                  </div>
                  <div className='d-flex align-items-start features-items'>
                    <img src='image/Guidance.svg' alt='' className='img-fluid' />
                    <p className='paragarph'>Additional custom measurement requirements can easily be added, for further understanding of soil health.</p>
                  </div>
                  <Link to='/stay-uplated' className='btn btn-primary mt-3 mt-md-4'>Read More</Link>
                </div>
              </div>
            </div>
            <div className='col-md-5'>
              <div className='image-section-hone'>
              <h2 className='section-heading mobile-view'>The future of in-field soil analysis</h2>
                <img src='image/future-img.png' className='img-fluid w-100'></img>
              </div>
            </div>
          </div>
        </div>
      </section>
      <section className='section-testimonils pt-100 bg-white'>
        <div className='container'>
          <div className='row align-items-center'>
            <div className='col-md-7'>
              <div className='customers-wrap'>
                <h2 className='section-heading'>See what our customers have to say</h2>
                <p className='paragarph'>Join over x customers who are already using Hone products across the globe.</p>
              </div>
            </div>
            <div className='col-md-5'>
              <div className='testimonils'>
                <div id="carouselExampleIndicators" class="carousel slide" data-ride="carousel">
                  <div class="carousel-inner">
                    <div class="carousel-item active">
                      <img class="d-block" src="image/testimonils.svg" alt="First slide" />
                      <div className='testimonils-card'>
                        <p className='paragarph'>Hone Carbon is highly innovative and knowledgeable on the use of spectroscopy for soils and has made immense progress towards reducing costs of soil carbon measurement. They have been extremely easy to work with, having an expansive view towards how to partner, to digitise landscape function and measure soil carbon sequestration.</p>
                      </div>
                      <div className='d-flex align-items-center'>
                        <img class="mr-4" src="image/testimonils-author.png" alt="First slide" />
                        <p className='paragarph m-0'>Customer name, grain corp</p>
                      </div>                     
                    </div>
                    <div class="carousel-item">
                      <img class="d-block" src="image/testimonils.svg" alt="First slide" />
                      <div className='testimonils-card'>
                        <p className='paragarph'>Hone Carbon is highly innovative and knowledgeable on the use of spectroscopy for soils and has made immense progress towards reducing costs of soil carbon measurement. They have been extremely easy to work with, having an expansive view towards how to partner, to digitise landscape function and measure soil carbon sequestration.</p>
                      </div>
                      <div className='d-flex align-items-center'>
                        <img class="mr-4" src="image/testimonils-author.png" alt="First slide" />
                        <p className='paragarph m-0'>Customer name, grain corp</p>
                      </div>                     
                    </div>
                    <div class="carousel-item">
                      <img class="d-block" src="image/testimonils.svg" alt="First slide" />
                      <div className='testimonils-card'>
                        <p className='paragarph'>Hone Carbon is highly innovative and knowledgeable on the use of spectroscopy for soils and has made immense progress towards reducing costs of soil carbon measurement. They have been extremely easy to work with, having an expansive view towards how to partner, to digitise landscape function and measure soil carbon sequestration.</p>
                      </div>
                      <div className='d-flex align-items-center'>
                        <img class="mr-4" src="image/testimonils-author.png" alt="First slide" />
                        <p className='paragarph m-0'>Customer name, grain corp</p>
                      </div>                     
                    </div>
                  </div>

                  <Link class="carousel-control-prev" to="#carouselExampleIndicators" role="button" data-slide="prev">
                    <span class="carousel-control-prev-icon" aria-hidden="true"></span>
                    <span class="sr-only">Previous</span>
                  </Link>
                  <Link class="carousel-control-next" to="#carouselExampleIndicators" role="button" data-slide="next">
                    <span class="carousel-control-next-icon" aria-hidden="true"></span>
                    <span class="sr-only">Next</span>
                  </Link>
                </div>
              </div>
            </div>
            <div className='col-md-12'>
              <div className='img-testimonials d-flex justify-content-center flex-wrap align-items-center'>
                <img src='image/testimonils-logo-1.svg' className='img-fluid' data-aos="flip-left" data-aos-duration="1000"/>
                <img src='image/testimonils-logo-2.svg' className='img-fluid' data-aos="flip-left" data-aos-duration="1000"/>
                <img src='image/testimonils-logo-3.svg' className='img-fluid' data-aos="flip-left" data-aos-duration="1000"/>
              </div>
            </div>
          </div>
        </div>
      </section>
      {/* <section className='slider-team'>
        <div className='container'>
          <div className='row'>
          <div className='col-md-12'>
              <h2 className='section-heading'>Our Team </h2>
            </div>
            <div className='col-md-12'>
              <Carousel responsive={responsive}>
                  {team.map((teamlist, index)=>{
                    return<Team teamlist={teamlist}/>
                  })}
              </Carousel>
            </div>
          </div>
        </div>
      </section> */}
    </div>
  );
}

export default Home;
