import React from 'react';
import { Link } from 'react-router-dom';
import SingleBlog from './single-blog';

const SinglePost = ({ bloglist }) => {

    return (
        <div>
            <article className="entry">
                <div className="entry-img">
                    <img src="./image/pdf-bg.png" alt={bloglist.title} className="img-fluid w-100" />
                </div>
                <div className='blog-cart-wrap'>
                    <h2 className="entry-title">
                        <a href={`/single-blog/${bloglist.id}`}>{bloglist.title}</a>
                    </h2>
                    <div className="entry-content">
                        <p>{bloglist.description}</p>
                        <div className="read-more">
                            <a href='#' data-toggle="modal" data-target={`#exampleModalLong${bloglist.id}`}>Download <i className="fa-solid fa-file-pdf"></i></a>
                        </div>
                    </div>
                </div>
                <div className="modal fade" id={`exampleModalLong${bloglist.id}`} tabindex="-1" role="dialog" aria-labelledby="exampleModalLongTitle" aria-hidden="true">
                    <div className="modal-dialog modal-dialog-centered" role="document">
                        <div className="modal-content">
                            <div className="modal-header border-0">
                                <h2 className="entry-title">
                                    <a href={`/single-blog/${bloglist.id}`}>Complete the details below to download the article.</a>
                                </h2>
                                <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                    <span aria-hidden="true">&times;</span>
                                </button>
                            </div>
                            <div className="modal-body">
                                <div className='blog-popup-wrap'>
                                    <p className='paragarph'>Once complete, youll be able to download the article immediately. We will only use your details to send you important updates about Hone. No spam, ever. </p>
                                    <form>
                                    <div class="form-group">
                                        <label for="name">Full name</label>
                                        <input type="text" class="form-control" id="exampleInputEmail1" aria-describedby="textHelp" placeholder="Enter your name"/>
                                        <small id="textHelp" class="form-text text-muted">We'll never share your name with anyone else.</small>
                                    </div>
                                    <div class="form-group">
                                        <label for="exampleInputEmail1">Email</label>
                                        <input type="email" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter your email e.g info@honeag.com"/>
                                        <small id="emailHelp" class="form-text text-muted">We'll never share your email with anyone else.</small>
                                    </div>
                                    <div class="form-group">
                                        <label for="exampleInputEmail1">Phone</label>
                                        <input type="email" class="form-control" id="exampleInputEmail1" aria-describedby="contactHelp" placeholder="+61 "/>
                                        <small id="contactHelp" class="form-text text-muted">We'll never share your phone with anyone else.</small>
                                    </div>
                                    <a type="submit" href={bloglist.pdf} target="_blank" class="btn btn-primary" download>Download <i className="fa-solid fa-file-pdf"></i></a>
                                </form>
                                </div>                                
                            </div>
                        </div>
                    </div>
                </div>
            </article>

        </div>
    );
}

export default SinglePost;
