import { blog } from "../data/data";
import SinglePost from './single-post';
import { Link } from 'react-router-dom';
import React, { useState, useCallback, useEffect } from 'react'

const About = () => {
  const [maxRange, setMaxRange] = useState(9);
  const loadMore = useCallback((event) => {
    setMaxRange(prevRange => prevRange + 3);
    event.preventDefault();
  },[])
  useEffect(() => {
    window.scrollTo(0, 0)
  }, [])
  return (
    <div className='page-wrapper'>
      <section className='our-blog-banner pb-3'>
        <div className='container'>
          <div className='row align-items-center'>
            <div className='col-md-6'>
              <h2 className='section-heading'>Research and Design</h2>
              <p className='paragarph-heading'>Accusantium sed ut perspiciatis unde omnis iste natus error sit voluptatem accusantium doloremque laudantium, totam rem aperiam.</p>
            </div>
            <div className='col-md-6'>
              <img src='./image/blog-banner.png' alt='' className='img-fluid w-100' />
            </div>
          </div>
        </div>
      </section>
      <section className='blog-items'>
        <div className='container'>
          <div className='row'>
            <div className='col-md-12'>
              <div className='heading-and-search'>
                <h3>Academic Articles</h3>
                <form className='serach-box'>
                  <input type="text" className='form-control' placeholder="Search"/>
                  <button className='button'><i class="fa-solid fa-magnifying-glass"></i></button>
                </form>
              </div>
            </div>
            <div className='col-md-12'>
              <div className='blog-post'>
                {blog.slice(0, maxRange).map((bloglist, index) => {
                  return <SinglePost bloglist={bloglist} />
                })}
              </div>
              {/* <div className='loadmore text-center py-5'>
                <a href='#' className='btn btn-primary' onClick={loadMore}>View More</a>
              </div> */}
            </div>
          </div>
        </div>
      </section>
    </div>
  );

}

export default About;
