import React, { useState, useCallback,useEffect } from 'react'
import { Link } from 'react-router-dom';

const About = () => {
  const [maxRange, setMaxRange] = useState(9);
  const loadMore = useCallback((event) => {
    setMaxRange(prevRange => prevRange + 3);
    event.preventDefault();
  }, [])
  useEffect(() => {
    window.scrollTo(0, 0)
  }, [])
  return (
    <div className='page-wrapper'>
      <section className='stay-updated-wrap mt-80'>
        <div className='container'>
          <div className='row align-items-center'>
            <div className="col-md-7">
              <h2 className='heading-contact mb-4'>Hone Support</h2>
              <p className='paragarph-16 mb-0'>Need help from our team?</p>
              <p  className='paragarph-16'>You can reach out to us via the form below, via email <a href="mailto:help@honeag.com" className="text-danger font-weight-bold">help@honeag.com</a>, or call us on <a href="tel:+61 2 4036 3664" className="text-danger font-weight-bold">+61 2 4036 3664</a>.  We're here to help 9am - 5pm Monday - Friday (excluding Public Holidays). </p>
              <p  className='paragarph-16'>Visit the knowledge base for guidance on how to use all Hone products.</p>
            </div>
            <div className="clear-both"></div>
            <div className='col-md-6'>
              <div className="form-wrapper-get">
                <h3 className="font-weight-bold" data-aos="zoom-in-up" data-aos-duration="1500">Get in touch</h3>
                <form data-aos="zoom-in-left" data-aos-duration="1000">
                  <div class="form-group">
                    <label for="name">Full name</label>
                    <input type="text" class="form-control" id="exampleInputEmail1" aria-describedby="textHelp" placeholder="Enter your name" />
                    <small id="textHelp" class="form-text text-muted">We'll never share your name with anyone else.</small>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputEmail1">Email</label>
                    <input type="email" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter your email e.g info@honeag.com" />
                    <small id="emailHelp" class="form-text text-muted">We'll never share your email with anyone else.</small>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputEmail1">Phone</label>
                    <input type="email" class="form-control" id="exampleInputEmail1" aria-describedby="contactHelp" placeholder="+61 " />
                    <small id="contactHelp" class="form-text text-muted">We'll never share your phone with anyone else.</small>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputEmail1">What products are you enquiring about? (Optional)</label>
                  </div>
                  <div class="form-check form-check-inline">
                    <input class="form-check-input" type="radio" id="inlineCheckbox1" value="option1" name="enquiring-about"/>
                      <label class="form-check-label" for="inlineCheckbox1">Hone Lab Red</label>
                  </div>
                  <div class="form-check form-check-inline">
                    <input class="form-check-input" type="radio" id="inlineCheckbox2" value="option2" name="enquiring-about"/>
                      <label class="form-check-label" for="inlineCheckbox2">Hone Lab Black</label>
                  </div>
                  <div class="form-check form-check-inline">
                    <input class="form-check-input" type="radio" id="inlineCheckbox3" value="option3" name="enquiring-about"/>
                      <label class="form-check-label" for="inlineCheckbox3">Hone Carbon</label>
                  </div>
                  <div class="form-group mt-4">
                    <label for="exampleInputEmail1">How can we help you?</label>
                    <textarea rows={5} class="form-control"></textarea>
                    <small id="contactHelp" class="form-text text-muted">We'll never share your phone with anyone else.</small>
                  </div>
                  <div className='d-block mt-md-5 mt-3'>
                    <button type="submit" href="#" target="_blank" class="btn btn-primary">Submit</button>
                  </div>
                </form>
              </div>
            </div>
            <div className='col-md-6'>
              <div className="text-center" data-aos="zoom-in-up" data-aos-duration="1500">
                <img src='./image/hone-support-img.png' alt='' className='img-fluid' />
              </div>
              <div className="knowledge-base text-center">
                <h3 className="font-weight-bold mb-4">Visit the knowledge base</h3>
                <p  className='paragarph-16'>Visit our knowledge base for step-by-step instructions and user guides on all Hone products.</p>
                <Link to="/"><img src='./image/right-arrow.svg' className='img-fluid' data-aos="zoom-in-up" data-aos-duration="1500"></img></Link>
              </div>
              
            </div>            
          </div>
        </div>
      </section>
    </div>
  );

}

export default About;
