import { team } from '../data/data';
import Team from './team';
import { Link } from 'react-router-dom';
import React, { useState, useCallback, useEffect } from 'react'

const About = () => {
  // const [maxRange, setMaxRange] = useState(9);
  // const loadMore = useCallback((event) => {
  //   setMaxRange(prevRange => prevRange + 3);
  //   event.preventDefault();
  // },[])
  useEffect(() => {
    window.scrollTo(0, 0)
  }, [])
  return (
    <div className='page-wrapper'>
      <section className='our-technologes mt-80'>
        <div className='container'>
          <div className='row'>
            <div className='col-md-6'>
              <h2 className='section-heading'>Our Team</h2>
              <p className='paragarph-heading'>Each member of the Hone team brings something different to the table, enabling development of Hones products and technology.</p>
            </div>
          </div>
          <div className='row'>
            <div className='col-md-12'>
              <div className='team-members'>
                {team.slice(0, 9).map((teamlist, index) => {
                  return <Team teamlist={teamlist} />
                })}
              </div>  
              {/* <div className='loadmore text-center py-5'>
                <a href='#' className='btn btn-primary' onClick={loadMore}>View More</a>
              </div>             */}
            </div>
          </div>
        </div>
      </section>
      <section className='pt-0'>
        <div className='container'>
            <div className='row'>
              <div className='col-md-12'>
                <div className='get-in-touch d-flex flex-wrap align-items-center justify-content-between'>
                  <div className='text-get-in-touch'>
                    <h5>Interested in joining us?</h5>
                    <p className='paragarph'>Join the Hone team to help us on our mission to empower people to make better decisions. Get in touch today to discuss future opportunities with Hone.</p>
                  </div>
                  <div>
                    <Link to='/contact-us' className='btn btn-primary mt-3'>Get in touch</Link>
                  </div>                  
                </div>
              </div>
            </div>
        </div>
      </section>
    </div>
  );

}

export default About;
