import React, { useState, useCallback, useEffect } from 'react'

const About = () => {
  const [maxRange, setMaxRange] = useState(9);
  const loadMore = useCallback((event) => {
    setMaxRange(prevRange => prevRange + 3);
    event.preventDefault();
  }, [])
  useEffect(() => {
    window.scrollTo(0, 0)
  }, [])
  return (
    <div className='page-wrapper'>
      <section className='stay-updated-wrap'>
        <div className='container'>
          <div className='row align-items-center'>
            <div className='col-md-6'>
              <img src='./image/contact-us.png' className='img-fluid'></img>
            </div>
            <div className='col-md-6'>
              <h2 className='heading-contact' data-aos="zoom-in-up" data-aos-duration="1500">Contact Us</h2>
              <p className='paragarph-18'>Have a question, comment or want to join an event? Get in touch with us today.</p>
              <form data-aos="zoom-in-left" data-aos-duration="1000">
                <div class="form-group">
                  <label for="name">Full name</label>
                  <input type="text" class="form-control" id="exampleInputEmail1" aria-describedby="textHelp" placeholder="Enter your name" />
                  <small id="textHelp" class="form-text text-muted">We'll never share your name with anyone else.</small>
                </div>
                <div class="form-group">
                  <label for="exampleInputEmail1">Email</label>
                  <input type="email" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter your email e.g info@honeag.com" />
                  <small id="emailHelp" class="form-text text-muted">We'll never share your email with anyone else.</small>
                </div>
                <div class="form-group">
                  <label for="exampleInputEmail1">Phone</label>
                  <input type="email" class="form-control" id="exampleInputEmail1" aria-describedby="contactHelp" placeholder="+61 " />
                  <small id="contactHelp" class="form-text text-muted">We'll never share your phone with anyone else.</small>
                </div>
                <div class="form-group">
                  <label for="exampleInputEmail1">Type your message</label>
                  <textarea rows={5} class="form-control"></textarea>
                  <small id="contactHelp" class="form-text text-muted">We'll never share your phone with anyone else.</small>
                </div>
                <div className='d-block mt-md-5 mt-3'>
                  <button type="submit" href="#" target="_blank" class="btn btn-primary">Submit</button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </section>
    </div>
  );

}

export default About;
